.cart_item {
    border-top: 2px solid #EDEDEF;
    background: #fff;
    width: 100%;
    position: relative;
}

.cart_quantity {
    height: 20px;
    display: inline-block;
    text-align: center;
    font-size: 24px;
    margin: 60px 50px;
    border: 1px solid #ededed;
    padding: 20px;
}

.cart_item_label > a {
    color: #4A4A4A;
    text-decoration: none;
}
.cart_item_label {
    padding: 20px 0 0 10px;
    width: calc(99% - 165px);
    display: inline-block;
    vertical-align: top;
    position: relative;
}
.item_pricebar {
    margin-top: 20px;
}
