.inventory_container {
    overflow: hidden;
    margin-bottom: 30px;
}

.product_sort_container {
    position: absolute;
    right: 20px;
    top: 23px;
    height: 35px;
    width: 115px;
}

.inventory_list {
    max-width: 1280px;
    margin: 0 auto;
    padding: 0 15px 30px;
}

.product_label {
    position: absolute;
    left: 110px;
    top: 25px;
    font-size: 2.2em;
    font-weight: 100;
    color: #fff;
}

@media only screen and (min-width: 1200px) {
    .inventory_container {
        padding-top: 40px;
    }
}
